.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background: rgba(0, 0, 0, 0.75);
}

.modal {
  position: fixed;
  top: 30vh;
  left: 10%;
  width: 80%;
  z-index: 100;
  overflow: hidden;
  background-color: #000;
  color: #fff;
  border: 1px solid cadetblue;
}
@media only screen and (max-width: 600px) {
  .modal {
    top: 15vh;
    left: 5%;
    width: 90%;
  }
}
